import { createStore } from 'vuex';

const store = createStore({
  state: {
    userName: '',
    trainersList: []
  },
  mutations: {
    setTrainersList(state, data) {
      state.trainersList = data;
    }
  },
  getters: {
    getUserName: state => state.userName,
    getTrainersList: state => state.trainersList,
  },
  actions: {

  },
  modules: {

  }
});

export default store;
